import React from 'react';
import { CardRent } from 'components';
import { RentList, Rent } from './styled'
const Component = ({ dataRents }) => {
    const dataVerify = dataRents.length > 0;
    console.log(dataRents)
    return (
        <RentList>
            {dataVerify ? null : <div><h1>No has rentado ningun producto</h1> <a href="/">hazlo ahora</a></div>}

            {dataRents.map(rent =>
                <Rent key={`product-${rent.idProduct}`}>
                    <CardRent
                        dataProduct={rent.dataProduct}
                        rentDetails={rent.rentDetails}
                        dataLessor={rent.dataLessor}
                    />
                </Rent>
            )}
        </RentList>
    );
}

export default Component;
export { Component as RentList }